

































.list-wrapper {
  width: 84%;
  -webkit-transition: all .4s;
  transition: all .4s;
}

.list-container li {
  border-bottom: 1px solid #eee;
}

.list-title {
  font-size: 26px;
  font-weight: 700;
  color: #404040;
  margin-top: 0;
}

.list-abstract {
  font-size: 16px;
  color: #919191;
}

.list-updated {
  font-family: "Comic Sans MS", curslve, sans-serif;
  font-size: 18px;
  color: #8b8b8b;
  margin-bottom: 0;
}

.list-container li a {
  padding: 1rem 2rem;
  background-color: #fff;
  display: block;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.list-container li a:hover {
  background-color: #ededed;
}






